import { Subject, merge } from 'rxjs';
import { withLatestFrom } from 'rxjs/operators';
import { evaluatePassword } from '../utils/password';
import { IDS } from '../views';
const { HOME_ID, SHOP_ID, PASSWORD_ID, EXERCISE_ID } = IDS;

// Changes to the global state
const commandSubject = new Subject();
const command$ = commandSubject.asObservable();
const applyState = newState => commandSubject.next(state => ({...state, ...newState}));

// Changes to the mode based state
const subCommandSubject = new Subject();
const subCommand$ = subCommandSubject.asObservable();
const applySubstate = (key, newSubstate) => subCommandSubject.next({key, command: state => ({...state, ...newSubstate})});

// Changes to the mode setting
const changeModeSubject = new Subject();
const changeMode$ = changeModeSubject.asObservable();
const changeMode = mode => changeModeSubject.next(state => ({...state, ...mode}));

const navigate = nextView => applySubstate('navigation', {currentView: nextView});

export const navigateExercise = title => navigate({...EXERCISE_ID, title});
export const navigateHome = () => navigate(HOME_ID);
export const navigateShop = () => navigate(SHOP_ID);
export const navigatePassword = () => navigate(PASSWORD_ID);

export const showChangeModePrompt = open => changeMode({showPrompt: !!open});
export const setMode = ({selectedLevel, selectedText}) => {
  changeMode({selectedLevel, selectedText, showPrompt: false});
  navigate(HOME_ID);
}

export const unlockShop = () => applySubstate('unlockables', {shopUnlocked: true});

export const toggleTheme = () => changeModeSubject.next(state => ({...state, theme: state.theme === 'dark' ? 'light' : 'dark'}))

export const enterPassword = (password = '') => commandSubject.next(state => {
  const { mode, unlockables } = state;
  const formattedPassword = password.toLowerCase();
  const { isNew, isUsed } = evaluatePassword({password: formattedPassword, mode, unlockables});
  if (isNew) {
    alert('You have unlocked a new exercise!');
    return {...state, unlockables: {...unlockables, password: formattedPassword}};
  } else if (isUsed) {
    alert('You have already unlocked this exercise.'); // TODO Alerts shouldn't be here. They should be in the view
  } else {
    alert('Sorry, I did not recognise that prompt.');
  }
  return state;
});

export function getModeState(state) {
  const { mode = {} } = state;
  const { selectedLevel, selectedText, showPrompt, theme } = mode;
  const { [selectedLevel]: { [selectedText]: localState = {}} = {} } = state;
  const { navigation = {}, unlockables = {} } = localState;
  return { navigation, unlockables, mode: { selectedLevel, selectedText, showPrompt, theme } };
}

export const changeFactory = store$ => merge(
  changeMode$.pipe(
    withLatestFrom(store$, (command, store) => {
      const { mode = {} } = store;
      return {...store, mode: command(mode)};
    })
  ),
  command$.pipe(
    withLatestFrom(store$, (command, store) => {
      const localState = getModeState(store);
      const newLocalState = command(localState);

      const { mode } = store;
      const { selectedLevel, selectedText } = mode;

      store[selectedLevel][selectedText] = newLocalState;

      return { ...store };
    }),
  ),
  subCommand$.pipe(
    withLatestFrom(store$, ({key, command}, store) => {
      const localState = getModeState(store);
      const newSubstate = command(localState[key]);
      const newLocalState = {
        ...localState,
        [key]: newSubstate
      };
      const { mode } = store;
      const { selectedLevel, selectedText } = mode;

      store[selectedLevel][selectedText] = newLocalState;
      return { ...store };
    })
  )
);